

import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import { userActions } from '../_actions';

class LoginPage extends React.Component {
    constructor(props) {
        super(props);

        // reset login status
        this.props.logout();

        this.state = {
            username: '',
            password: '',
            submitted: false,
            record: false,
            sound: null
            
        };
        this.startRecording = this.startRecording.bind(this)
        this.stopRecording = this.stopRecording.bind(this)
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
   startRecording(){
       this.setState({
           record:true
       });
   };
   stopRecording(){
    this.setState({
        record:false
    });
};
    handleChange(e) {
        const { name, value } = e.target;
        this.setState({ [name]: value });
    }

    handleSubmit(e) {
        e.preventDefault();

        this.setState({ submitted: true });
        const { username, password } = this.state;
        if (username && password) {
            this.props.login(username, password);
        }
    }

    render() {
        const { loggingIn } = this.props;
        const { username, password, submitted } = this.state;
        return (
            
            <div className="col-md-6 col-md-offset-3" id="form-control">
                    <label className="label">Exquisite Corpse Sound Bath</label>
                    <br></br>
                    <br></br>
                    <br></br>
                <h2>Login</h2>
                <form name="form" onSubmit={this.handleSubmit}>
                    <div className={'form-group' + (submitted && !username ? ' has-error' : '')}>
                        <label htmlFor="username">Username</label>
                        <input type="text" className="form-control" name="username" value={username} onChange={this.handleChange} />
                        {submitted && !username &&
                            <div className="help-block">Username is required</div>
                        }
                    </div>
                    <div className={'form-group' + (submitted && !password ? ' has-error' : '')}>
                        <label htmlFor="password">Password</label>
                        <input type="password" className="form-control" name="password" value={password} onChange={this.handleChange} />
                        {submitted && !password &&
                            <div className="help-block">Password is required</div>
                        }
                    </div>
                    <div className="form-group">
                        <button className="btn btn-primary">Login</button>
                        {loggingIn &&
                            <img src="data:image/gif;base64," />
                        }
                        <Link to="/register" className="btn btn-link">Register</Link>
                    </div>
                </form>
                {/* <hero className="col-md-12"> */}
        {/* <h1 style={{display:'block', */}
                    {/* // color:'yellowgrey', */}
                {/* //    textAlign:'center', */}
                {/* //    fontSize:'30px', */}
                {/* //    animationName:pulse, */}
                {/* //    animationDuration:1.5, */}
                   {/*  */}
                {/* //    }}>Exquisite Sound<hr/> */}
        {/* <img style={{alignContent:'center' */}
                    {/* // }}src="https://i.pinimg.com/originals/d8/57/9c/d8579c08b29021bf3ffc4425b0718cf0.jpg" className="img-circle"></img> */}
        {/* </h1> */}
       {/* </hero> */}
            </div>
            
        );
        
    }
}

function mapState(state) {
    const { loggingIn } = state.authentication;
    return { loggingIn };
}

const actionCreators = {
    login: userActions.login,
    logout: userActions.logout
};

const connectedLoginPage = connect(mapState, actionCreators)(LoginPage);
export { connectedLoginPage as LoginPage };